import React, {Component} from 'react'

 export default class ContactC extends Component {
     render(){
       return (
        <section className="contact_area section_gap">
          <div className="container">
            <div className="row">
              <div className="col-lg-3">
                <div className="contact_info">
                  <div className="info_item" data-animate-effect="fadeInLeft">
                    <i className="lnr lnr-home" />
                    <h6>Bengaluru, India</h6>
                    <p>Available at BMSCE</p>
                  </div>
                  <div className="info_item" data-animate-effect="fadeInLeft">
                    <i className="lnr lnr-phone-handset" />
                    <h6><a href="#">(+91) 9538744118</a></h6>
                    <p>Available anytime</p>
                  </div>
                  <div className="info_item" data-animate-effect="fadeInLeft">
                    <i className="lnr lnr-envelope" />
                    <h6><a href="gmail.com">obedjunias.cs17@bmsce.ac.in</a></h6>
                    <p>Ping me anytime!</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-9">
                <form className="row contact_form" action="https://formspree.io/xvokdrze" method="post" id="contactForm" noValidate="novalidate">
                  <div className="col-md-6">
                    <div className="form-group">
                      <input type="text" className="form-control" id="name" name="name" placeholder="Enter your name" />
                    </div>
                    <div className="form-group">
                      <input type="email" className="form-control" id="email" name="email" placeholder="Enter email address" />
                    </div>
                    <div className="form-group">
                      <input type="text" className="form-control" id="subject" name="subject" placeholder="Enter Subject" />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="form-group">
                      <textarea className="form-control" name="message" id="message" rows={1} placeholder="Enter Message" defaultValue={""} />
                    </div>
                  </div>
                  <div className="col-md-12 text-right">
                    <button type="submit" value="submit" className="primary_btn">
                      <span>Send Message</span>
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </section>
      );
  
     }
 }